import { Dispatch, SetStateAction } from "react"
import { FileRowType } from "@rp/common/utils"
import { useFileActions } from "@rp/react/web/common/files"
import { SelectedFile } from "../../files.types"

type HandleFileRowClickRowFuncProps = {
  actions: ReturnType<typeof useFileActions>["actions"]
  item: FileRowType
  isSelected: boolean
  isThisFileOpen: boolean
  openChatPane: () => void
  setSelectedFiles: Dispatch<SetStateAction<Array<SelectedFile>>>
  shared: boolean
}

function handleFileRowClickRowFunc({
  actions,
  item,
  isSelected,
  isThisFileOpen,
  openChatPane,
  setSelectedFiles,
  shared,
}: HandleFileRowClickRowFuncProps): void {
  if (!isSelected) {
    setSelectedFiles(prev => [{ id: item.id, shared: !!shared }, ...prev])
    actions.open.onClick(item)
    return
  }

  if (isThisFileOpen) {
    actions.close.onClick()
    return
  }

  openChatPane()
  actions.open.onClick(item)
}

export { handleFileRowClickRowFunc }
